import React from 'react';
import { object, bool } from 'prop-types';
import { compose, withProps } from 'recompose';
import { withStyles } from 'material-ui/styles';
import withWidth, { isWidthDown } from 'material-ui/utils/withWidth';
import Typography from 'material-ui/Typography';
import Helmet from 'react-helmet';

import withRoot from '../withRoot.js';
import Header from '../components/Header.jsx';

const styles = theme => ({
  '@global a': {
    color: theme.palette.primary[500],
  },
  body: {
    minHeight: '100vh',
    background: 'linear-gradient(150deg, #263238 0%,#263238 50%,#1d262a 50%,#1d262a 100%)',
  },
  content: {
    paddingTop: 56,
    [theme.breakpoints.up('sm')]: {
      paddingTop: 64,
    },
    [theme.breakpoints.up('md')]: {
      paddingTop: 0,
    },
  },
  wrapper: {
    maxWidth: 640,
    margin: '0 auto',
    padding: `${theme.spacing.unit * 10}px ${theme.spacing.unit * 2}px`,
    [theme.breakpoints.up('sm')]: {
      padding: `${theme.spacing.unit * 10}px ${theme.spacing.unit * 2}px`,
    },
  },
});

class App extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <div className={this.props.classes.body}>
        <Helmet>
          <title>Prospero</title>
        </Helmet>
        <Header noLinks smDown={this.props.showMenu} />
        <div className={this.props.classes.wrapper}>
          <Typography type="display1" paragraph>Privacy Policy</Typography>
          <Typography type="title" paragraph>
            The privacy and security of your personal information is extremely important to C&amp;T.  We are committed to protecting your personal information and being transparent about the information we hold.  This privacy notice explains how and why we use your personal data so that you can be confident about giving us your information.
          </Typography>
          <Typography type="headline" paragraph>Who are we?</Typography>
          <Typography type="body1" paragraph>
              We are Collar And Tie Limited and our working name is C&amp;T. We are registered as a limited company, number: 2878790 and a registered charity, number: 1085653.  Our registered office address is: University of Worcester, Henwick Grove, Worcester, WR2 6AJ.
          </Typography>
          <Typography type="headline" paragraph>What Personal Data do we collect?</Typography>
          <Typography type="body1" paragraph>
            Your personal data (any information which identifies you, or which can be identified as relating to you personally for example, name, address, phone number, email address) will be collected and used by us. We’ll only collect the personal data that we need.
          </Typography>
          <Typography type="body1" paragraph>
            By registering to be sent information about Prospero, you will give us your personal information – name and email address.
          </Typography>
          <Typography type="headline" paragraph>Under 18?</Typography>
          <Typography type="body1" paragraph>
            If you are under 18, please ensure that you have your parent/guardian’s permission before you provide us with your personal data.
          </Typography>
          <Typography type="headline" paragraph>What is the legal basis for processing your personal data?</Typography>
          <Typography type="body1" paragraph>
            When we ask for your personal information, it will only be in response to your request for information about Prospero.  Therefore, the legal basis for processing your personal data is consent.  By consenting to give us your personal information, you are giving us the permission to process your personal data specifically to receive information about Prospero.
          </Typography>
          <Typography type="headline" paragraph>How will we use your personal data?</Typography>
          <Typography type="body1" paragraph>
            We will use your personal data to allow us to send you details and updates about the digital engagement technology that is Prospero.
          </Typography>
          <Typography type="headline" paragraph>Will your personal data be shared with any third parties?</Typography>
          <Typography type="body1" paragraph>
            C&amp;T will not pass on your personal data to any third parties unless required to do so by the law or by a regulatory authority.
          </Typography>
          <Typography type="headline" paragraph>How long will we store your personal data?</Typography>
          <Typography type="body1" paragraph>
            Your personal data will be held for as long as you subscribe to receive information and updates about Prospero and for no longer than is reasonable and necessary.
          </Typography>
          <Typography type="headline" paragraph>What are your rights? </Typography>
          <Typography type="body1"paragraph>
            You have the right to ask if we have any personal data on you and to see that personal data; this is called a subject access request.  Subject access requests should be sent by email to info@candt.org or by post to The Data Protection Officer, Collar and Tie Limited, University of Worcester, Henwick Grove, Worcester, WR2 6AJ.
          </Typography>
          <Typography type="headline" paragraph>Changes to our privacy notice</Typography>
          <Typography type="body1" paragraph>
            We regularly review our privacy notice and may need to make changes from time to time.  Any changes made will be posted on this page.
          </Typography>
          <Typography type="headline" paragraph>Contact</Typography>
          <Typography type="body1" paragraph>
            We welcome any questions, comments or requests regarding this privacy notice.  We can be contacted at <a href="mailto:info@candt.org">info@candt.org</a> or at our registered office address.
          </Typography>
        </div>
      </div>
    );
  }
}

App.propTypes = {
  classes: object,
  showMenu: bool,
};

export default compose(
  withRoot,
  withStyles(styles),
  withWidth(),
  withProps(({ width }) => ({ showMenu: isWidthDown('sm', width) })),
)(App);



